                <p>Buwan ng Wika Is a month long event celebrating Filipino language and culture. Throughout the month there will be activities like declamations, poetry readings, musical performances, and the much anticipated Lakan at Lakambini high school pageant celebrating the virtues and beauty of our national heritage. Click here to see a video performance of our grade school students.</p>
